import React from 'react';
import Styles from './GetCodeModal.module.css';
import Video from './../../Assets/CodePasteIntoEditor.mp4';

const GetCodeModal = (props) => {
    return (
        <div className={Styles.container}>
            <div>
                <h3 className={Styles.text}>
                    Kod dla EV3 zosta automatycznie skopiowany.
                </h3>
                <p className={Styles.text}>
                    Otwrz program 'Christmas Ball Decorator'
                    i&nbsp;przejd do zakadki <b>'Instructions'</b>. Usu bloczek tablicy i&nbsp;wklej nowy na jego
                    miejsce jak pokazano obok.
                </p>
                <p className={Styles.text}>
                    Jeeli nie moesz wklei nowego bloczka do&nbsp;edytora EV3 to&nbsp;prawdopodobnie kod
                    nie&nbsp;zosta
                    automatycznie skopiowany.
                    W&nbsp;takim przypadku skopiuj cay tekst z&nbsp;poniszego pola tekstowego.
                </p>
                <p className={Styles.center}><code>CTRL + A, CTRL + C</code></p>
                <textarea className={Styles.textarea} rows='4' readOnly value={props.code}/>
            </div>
            <div>
                <video className={Styles.video} height='400' width='450' autoPlay loop>
                    <source src={Video} type='video/mp4'/>
                    Nie mona odtworzy wideo.
                </video>
            </div>
        </div>
    )
};

export default GetCodeModal;