import React from 'react';
import Styles from './StartupModal.module.css';
import Video from './../../Assets/Drawing.mp4';

const StartupModal = () => {
    return (
        <div className={Styles.container}>
            <div>
                <h3 className={Styles.text}>
                    Witaj w edytorze ozdb!
                </h3>
                <p className={Styles.text}>
                    Aby namalowa wasny wzr uyj myszy:
                </p>
                <p className={Styles.text}><code>Lewy przycisk myszy</code> - Maluj</p>
                <p className={Styles.text}><code>Prawy przycisk myszy</code> - Gumka</p>
                <p className={Styles.text}>
                    Moesz te wczyta gotowy obrazek, wystarczy wcisn <code>Wybierz obrazek</code>.
                </p>
                <p>
                    Gdy ju skoczysz projektowa ozdob nacinij <code>Pobierz kod dla EV3</code>.
                </p>
            </div>
            <div>
                <video className={Styles.video} height='222' width='228' autoPlay loop>
                    <source src={Video} type='video/mp4'/>
                    Nie mona odtworzy wideo.
                </video>
            </div>
        </div>
    )
};

export default StartupModal;